.btn{
  width: 30vw;
  margin-top: 30px;
  background-color: #ee8529;
  border-radius: 25px;
  width: 220px;
  padding: 7px;
  color: #ffdaaa;
  
}
.btnP{
  width: 30vw;
  margin-top: 30px;
