}
h1, h3, h4, h5{
 color:  #b89e61
}
#lineConacted span:before,
#lineConacted span:after {
  content: "";
  position: absolute;
  height: 5px;
  border-bottom: 1px solid white;
  border-top: 1px solid white;
  top: 0;
  width: 600px;
}
input{
  width: 80%;
  margin: 2%;
  font-size: 120%;
  border: solid 1px #b89e6181;
  padding: 5px;
  padding-right: 10px;
}
.submit{
  background-color: #ee8529;
  border-radius: 25px;
  float: left;
  margin-left: 8%;
  width: 25%;
  color: #ffdaaa;
}

::placeholder{
  color: #c0c0c0;
  font-family: 'Assistant', sans-serif;
  font-size: 90%
}


.lineConacted {
  line-height: 0.5;
  text-align: center;
}
.lineConacted span {
  display: inline-block;
  position: relative;  
}
.lineConacted span:before {
  right: 100%;
  margin-right: 15px;
}
.lineConacted span:after {
  left: 100%;
  margin-left: 15px;
}
p, h1, h3, h4, h5, img{
  width: 80%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  

}
