.mainNav{
    width: 100%;
    z-index: 1000;
  }
  .menu {
    table-layout: fixed;
    width: 100%;

  
  }
  .nav{
    width: 78%;

  }
  .textNL{
    color: #74422b;
    font-size: 100%;
    margin: 3px;
    white-space: nowrap;

  }

  
  
  #menu {

    position: static;
    display: block;
    margin-bottom: auto;
    overflow: hidden;
    margin-top: 10px;
    float: left;
    width: 600px;
    /* border: solid 1px #b42323; */
  
  }
  
  
  .navbar {
    display:none;
    margin-left: 2%;
    
  }
  
  /* .logo:hover, .navbarLogo:hover{
    margin-top: 1px;
    margin-bottom: 2px;
    box-shadow: 1px 1px 3px #888888;
  } */

  #logoDiv{
    position: fixed;
    overflow: hidden;
    width: 100%;
    padding: 4px;
    background-color: #f1f1f1;
  }
 
  .logo {
    float: right;
    width: 140px;
    margin-top: 3px;
  }
  .navlink {
    color: #74422b;
    text-align: center;
    border-radius: 10px;
    margin-right: 4%;
    float: right;
    transition: ease-in-out 0.2s;
    font-size: 130%;
    margin-bottom: auto;
    margin-top: auto;

  }
  .textNL:hover{
   text-decoration: underline;
  }
  input[type=checkbox]{
    display: none;
    -webkit-appearance: none;
  }
  
  input[type=checkbox]:checked ~ #menu{
    display: block;
  }
  li.navlink:checked ~ #menu{
    display: block;
  }
  
@media script, (max-width : 760px){
	#menu {

	position: static;
    display: none;
    margin:0%;
    padding: 0%;
    width: 100%;
    float: center;

	}
    .navlink {
      margin: 0;
    font-size: 130%;
    margin-bottom: 2px;
    width: 98%;
    text-align: center;
    background-color:  #b8b8b83a;

    }
    .logo {
        float: right;
        width: 100px;
    }
    .navbarLogo{
        float: left;
        width: 45px;
        margin-top: 3px;
    
    }
    .navbar {
            display:block;
    }
    .textNL{
      color: #74422b;
      font-size: 100%;
      margin: 0;
      margin-top: 7px;
      margin-bottom: 7px;
      white-space: nowrap;
      width: 100%
    }
    

}